.main-page {
  h2 {
    margin-right: 12px;
    color: #fff;
    font-weight: 400;
    text-transform: uppercase;
    font-size: x-large;
    margin: 20px 10px;
  }
  .movie-list {
    list-style: none;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 20px;

    .movie-item {
      border: 1px solid #ccc;
      border-radius: 5px;
      overflow: hidden;

      .movie-content {
        padding: 10px;
      }

      .movie-image {
        width: 100%;
        height: 300px;
        border-bottom: 1px solid #ccc;
        transition: transform 0.2s ease;
      }
      .movie-image:hover {
        transform: scale(1.1);
      }

      .movie-name {
        margin: 10px 0;
        font-size: 16px;
        color: #fff;
        text-decoration: none;
        transition: color 0.3s ease;

        &:hover {
          color: var(--primry-color);
        }
      }

      .movie-year {
        font-size: 14px;
        color: #666;
      }
    }
  }

  .pagination-container {
    padding: 20px;
    .pagination {
      display: flex;
      justify-content: center;
      list-style: none;
      padding: 0;
      margin-top: 20px;
      padding: 10px;
      flex-wrap: wrap; /* Cho phép các nút phân trang xuống dòng trên thiết bị di động */
    }

    li {
      margin: 5px; /* Giảm khoảng cách giữa các nút phân trang */
      cursor: pointer;

      a {
        text-decoration: none;
        color: #fff;
        background-color: #aa474c;
        padding: 10px 15px;
        border: 1px solid #ccc;
        border-radius: 5px;
        font-size: 14px;
        transition: background-color 0.3s ease;

        &:hover {
          background-color: #f0f0f0;
          color: #aa474c;
        }

        &.active {
          background-color: #ff6600;
          color: #fff;
          border: 1px solid #ff6600;

          &:hover {
            background-color: #ff6600;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .pagination {
    flex-wrap: wrap;
    margin-top: 5px;
  }

  li {
    margin: 5px 2px;
  }

  a {
    padding: 6px 8px;
  }
}
